import React from "react";
import "./ProjectCard.css";
import ProjectCardImage from "./ProjectCardImage/ProjectCardImage";
import ProjectCardText from "./ProjectCardText/ProjectCardText";

class ProjectCard extends React.Component {
	render() {
		return (
			<div
				className="project-card"
				style={{
					backgroundColor: this.props.project.backgroundColor,
					color: this.props.project.textColor,
				}}
			>
				<ProjectCardImage
					title={this.props.project.title}
				></ProjectCardImage>
				<ProjectCardText
					title={this.props.project.title}
					description={this.props.project.description}
					techUsed={this.props.project.techUsed}
					color={this.props.project.textColor}
					link={this.props.project.link}
				></ProjectCardText>
			</div>
		);
	}
}

export default ProjectCard;
