import Project from './Project/Project'
import Skills from "./Skills/Skills";
import Footer from "./Footer/Footer";

class Main extends React.Component {
	render() {
        return(
            <div className="after">
                <Intro></Intro>
                <About></About>
                <Project></Project>
                <Skills></Skills>
                <Footer></Footer>
            </div>
        )
	}
}

export default Main;
