import React from "react";
import "./Intro.css";

class Intro extends React.Component {
	links = [
		{
			link: "https://www.linkedin.com/in/pahulkalra/",
			icon: "fab fa-linkedin",
		},
		{
			link: "https://www.github.com/biohazard33",
			icon: "fab fa-github",
		},
		{
			link: "https://www.hackerrank.com/ElPsyKongroo",
			icon: "fab fa-hackerrank",
		},
	];

	index = [
		{
			link: "#about",
			text: "About",
		},
		{
			link: "#project",
			text: "Projects",
		},
		{
			link:
				"https://drive.google.com/file/d/1OhSXMXB_0h3Yux6ohhBu4wGNO4eKfrGF/view?usp=drive_open",
			text: "Resume",
			icon: "fas fa-external-link-alt",
			target: "_blank"
		},
	];

	render() {
		return (
			<div className="intro">
				<div className="intro-name">
					<span>Hi,</span>
					<span>My name is </span>
					<span>Pahul Kalra</span>
					<post>
						<i>Student | Developer</i>
					</post>
				</div>
				<div className="intro-right">
					<img src="./intro-guy.svg" alt="guy with laptop" className="intro-guy-image"></img>
					<div className="links">
						{	
							this.links.map((link, index) => (
								<a href={link["link"]} target="_blank" rel="noreferrer" key={index}>
									<i className={link["icon"]}></i>
								</a>
							))
						}
					</div>
					<div className="index">
						<ul>
							{
								this.index.map((item, index) => (
									<a href={item.link} key={index} target={item.target}>
										<li>{item.text}</li>
										{item.icon ? <i className={item.icon}></i> : ""}
									</a>
								))
							}
						</ul>
					</div>
				</div>
			</div>
		);
	}
}

export default Intro;
