import React from "react";
import "./Project.css";
import ProjectCard from "./ProjectCard/ProjectCard";

class Project extends React.Component {
	projects = [
		{
			title: "Shift-it",
            description:
                "Shift-It is a Chrome Extension bundled with an Android Application to help people easily share web pages from their PC/Laptop to their Android device with one click in real time.It is also available on the Chrome web Store for free use.",
            techUsed:['Node.js','FireBase','Android(Java)','HTML','CSS','Express'],
            link:'https://shift-it-psp.herokuapp.com/',
            textColor:'#ffffff',
            backgroundColor:'#000000'
        },
        {
            title:"FindMyBus",
            description:
                "FindMyBus is a MEAN stack based Web Application that helps teacher and students to track the school/college bus with ease. So that they can arrive on their bus stops neither too early nor too late.",
            techUsed:['Node.js','Express' , 'Angular' ,'MongoDB','Android(Java)',"Google Maps API"],
            textColor:'#ffffff',
            backgroundColor:'#CFD7E6',
        },
        {
            title: "Wunderlist",
            description:
                "Wunderlist is MEAN stack based To-Do App that helps everyone to organize their tasks easily. It supports user profile avatars and also shows the most recent SignUps on the homepage.",
            techUsed:['Node.js','Express' , 'Angular' ,'MongoDB'],
            link:'https://pahul-wunderlist.herokuapp.com/',
            textColor:'#ffffff',
            backgroundColor:'#3369FF'
        },
        {
            title: "MiniURL",
            description:
                "A minimal Web URL shortener that minifies the URL given by a Random Generated String or a string of User's choice.",
            techUsed:['Node.js','Express' , 'HTML' , 'CSS' ,'MongoDB'],
            link:'https://minweb.herokuapp.com/',
            textColor:'#ffffff',
            backgroundColor:'#18BD94'
        },
        {
            title: "Type-Attack",
            description:
                "Type-Attack is a HTML Canvas game built using the Phaser 3 Framework where you destroy your enemies by typing the word associated with the enemy using lasers. It has some more quirks, explore :)",
            techUsed:['HTML','CSS','JavaScript','HTML Canvas','Phaser 3'],
            link:'https://biohazard33.github.io/type-attack',
            textColor:'#ffffff',
            backgroundColor:'#090F14'
        }
	];

	render() {
		return (
			<div id="project">
				<span className="heading">PROJECTS</span>
				<div className="project-inner">
                    {
                        this.projects.map((project)=>{
                            return (<ProjectCard project={project}></ProjectCard>)
                        })
                    }
				</div>
			</div>
		);
	}
}

export default Project;
