import React from "react";
import "./About.css";
// import aboutImage from "../../../public/about.jpg";

class About extends React.Component {
	aboutme="My name is Pahul Kalra , I am currently pursuing my B.Tech from KIET Group of Institutions. I am currently in my final year and have worked on many projects. I am currently doing Web Development and want to chose Software Engineering as my career."
	render() {
		return (
			<div className="about" id="about">
				<span className="heading">ABOUT ME</span>
				<div className="about-inner">
					<div className="about-inner-image">
						<img src="./about.jpg" alt="about" className="about-image"></img>
					</div>
					<div className="about-inner-text">
                        <p className="text">
							{this.aboutme}
                        </p>
                    </div>
		    	</div>
            </div>
		);
	}
}

export default About;
