import React from "react";
import "./Footer.css";

class Footer extends React.Component {
    links = [
		{
			link: "https://www.linkedin.com/in/pahulkalra/",
			icon: "fab fa-linkedin",
		},
		{
			link: "https://www.github.com/biohazard33",
			icon: "fab fa-github",
		},
		{
			link: "https://www.hackerrank.com/ElPsyKongroo",
			icon: "fab fa-hackerrank",
		},
	];

	render() {
		return (
			<div id="footer">
				<span className="heading">FIND ME AT :</span>
				<div className="footer-inner">
					<div className="contact">
						<span><i className="fas fa-envelope"></i>pahulkalra33viii@gmail.com</span>
						<span><i className="fas fa-phone-alt"></i>+91 987 654 3210</span>
					</div>
                    <div className="links">
						{	
							this.links.map((link, index) => (
								<a href={link["link"]} target="_blank" rel="noreferrer" key={index}>
									<i className={link["icon"]}></i>
								</a>
							))
						}
					</div>
                    <p style={{fontFamily:"Caveat"}}>Designed with <i class="fas fa-heart"></i> By Pahul Kalra</p>
				</div>
			</div>
		);
	}
}

export default Footer;
