import React from "react";
import FrontMatter from './FrontMatter/FrontMatter';
import Main from './Main/Main'
import "./App.css"

class App extends React.Component {
	
	state={
		front:true
	}

	click=()=>{
		let temp=!this.state.front;
		this.setState({front:temp})
	}

	render()
	{
		if(this.state.front===true)
			return (
				<FrontMatter click={this.click}></FrontMatter>
			);
		else{
			return (
				<Main></Main>
			)
		}
	}
}

export default App;