import React from 'react'
import GrowingButton from './Button/GrowingButton';
import './FrontMatter.css';
import './Button/GrowingButton'

class FrontMatter extends React.Component{
    render(){
        return (
            <div className="before-container">
                <h1>You Shall Pass!</h1>
                <GrowingButton text="Enter" click={this.props.click}></GrowingButton>
            </div>
        )
    }
}

export default FrontMatter