import React from "react";
import "./ProjectCardText.css";

class ProjectCardText extends React.Component {
	render() {
		return (
			<div className="project-text-container">
				<span className="project-header" style={{color: this.props.color}}>
					<h2 className="project-title">{this.props.title}</h2>
                    <a href={this.props.link} style={{color:this.props.color}} target="_blank" rel="noreferrer"><i class="fas fa-link"></i></a>
				</span>
				<div className="project-description">{this.props.description}</div>
				<hr></hr>
				<div className="project-technologies">
					<b>TECH USED : {this.props.techUsed.join(", ")}</b>
				</div>
			</div>
		);
	}
}

export default ProjectCardText;
